import {HttpRequest} from '@angular/common/http';

import {TestRequest} from './request';

/**
 * Defines a matcher for requests based on URL, method, or both.
 *
 * @experimental
 */
export interface RequestMatch {
  method?: string;
  url?: string;
}
/**
 * Controller to be injected into tests, that allows for mocking and flushing
 * of requests.
 * 
 * \@experimental
 * @abstract
 */
export abstract class HttpTestingController {
/**
 * Search for requests that match the given parameter, without any expectations.
 * @abstract
 * @param {?} match
 * @return {?}
 */
match(match: string|RequestMatch|((req: HttpRequest<any>) => boolean)) {}
/**
 * @abstract
 * @param {?} url
 * @return {?}
 */
expectOne(url: string) {}
/**
 * @abstract
 * @param {?} params
 * @return {?}
 */
expectOne(params: RequestMatch) {}
/**
 * @abstract
 * @param {?} matchFn
 * @return {?}
 */
expectOne(matchFn: ((req: HttpRequest<any>) => boolean)) {}
/**
 * @abstract
 * @param {?} match
 * @return {?}
 */
expectOne(match: string|RequestMatch|((req: HttpRequest<any>) => boolean)) {}
/**
 * @abstract
 * @param {?} url
 * @return {?}
 */
expectNone(url: string) {}
/**
 * @abstract
 * @param {?} params
 * @return {?}
 */
expectNone(params: RequestMatch) {}
/**
 * @abstract
 * @param {?} matchFn
 * @return {?}
 */
expectNone(matchFn: ((req: HttpRequest<any>) => boolean)) {}
/**
 * @abstract
 * @param {?} match
 * @return {?}
 */
expectNone(match: string|RequestMatch|((req: HttpRequest<any>) => boolean)) {}
/**
 * @abstract
 * @param {?=} opts
 * @return {?}
 */
verify(opts?: {ignoreCancelled?: boolean}) {}
}
